.App {
  margin: 1%;
  border: 2px solid black;
  min-height: 100vh;
  display: grid;
  grid-template-rows: 100px 1fr;
  grid-template-columns: repeat(2, 1fr);
}

.header_div{
  background-color: #7ED2D2;
  grid-area: 1 / 1 / span 1 / span 2;
  color: #FFFFFF;
  width: auto;
  height: 100px;
  padding-top: 1%;
  padding-bottom: 1%;
}

.headertxt{
  float: left;
  font-family: 'Charm', cursive;
}

.logo_img{
  background-color: #7ED2D2;
  width: auto;
  height: 80px;
  float: left;
  margin-left: 2%;
}

.directoryView_div{
  grid-area: 2 / 1 / span 1 / span 1;
  height: 100vh;
  border-right: 2px solid black;
  display: grid;
  grid-template-rows: 100px 1fr;
  grid-template-columns: 1fr;
  margin-left: 5%;
  margin-right: 5%;
}

.searchForm_div{
  padding: 1%;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 120px;
  grid-gap: 10px;
}

#search{
  border: 1px solid black;
  border-radius: 2%;
}

.btn{
  margin-top: 20%;
}


.detailView_div{
  float: right;
  grid-area: 2 / 2 / span 1 / span 1;
}


.recipeCard_div{
  margin: 5%;
  padding: 0%;
  height: 100px;
  border: 1px solid black;
}

.recipeCard_div:hover{
  cursor: pointer;
  background-color: #7ED2D2;
  
} 
